import { createStore } from "vuex";

export default createStore({
  state: {
    pipeline_name_compute: "",
    quizList: [],
    examName: "",
    newsTitle:""
  },
  mutations: {
    change_pipe_name(state, value) {
      state.pipeline_name_compute = value;
    },
    changeQuizList(state, params) {
      state.quizList = params;
    },
    changeExamName(state, params) {
      state.examName = params;
    },
    changeNewsTitle(state, params) {
      state.newsTitle = params;
    },
  },
  actions: {},
  modules: {},
});
