import axios from "axios";
import jsonp from "jsonp";
let baseUrl = "http://192.168.239.22:80";

export function getExamList(examName) {
  return axios.get(`${baseUrl}/exam?examName=${examName}`);
}

export function getNewsList() {
  return axios.get(`${baseUrl}/home/articleList`);
}

export function getNewsDetail(newsTitle) {
  return axios.get(`${baseUrl}/home/articleDetail?newsTitle=${newsTitle}`);
}

export function getTodoList(todoList) {
   return axios.get(`${baseUrl}/todolist/find`);
}
export function delTodoList(id) {
  return axios.get(`${baseUrl}/todolist/del?id=${id}`);
}

export default {
  getExamList,
  getNewsList,
  getNewsDetail,
  getTodoList,
  delTodoList
};
