<script>
import axios from 'axios';
import {getTodoList} from '../assets/api/index'
import {delTodoList} from '../assets/api/index'
export default {
  data(){
    return{
      hasTodoList:false,
      isCreating:false,
      todoListRes:[],
      showCreate:false,
      createTime:'',
      todoDetail:'',
      planTime:'',
      beizhu:''
    }
  },
  async mounted(){
    this.findTodoList();
    console.log(this.hasTodoList)
  },
  methods:{
    // async sendTodoLists(){
    //   this.newtodolist.createTime = this.createTime;
    //   console.log(this.newtodolist)
    //   let res = await sendTodoList(JSON.stringify(this.newtodolist));
    //   console.log(res);
    // },
    enterZhishiku(){
      this.$router.push('/zhishiku');
    },
    enterRiskComputed(){
      console.log(111);
      this.$router.push('/riskComputed');
    },
    enterTodoList(){
      console.log(111);
      this.$router.push('/todoList');
    },
    enterHome(){
      console.log(111);
      this.$router.push('/home');
    },
    async sendTodoLists(){
        let formdata = new FormData();
        formdata.append('createTime',this.createTime);
        formdata.append('todoDetail',this.todoDetail);
        formdata.append('planTime',this.planTime);
        formdata.append('beizhu',this.beizhu);
        await axios({
          url:'http://192.168.239.22:80/todolist/add',
          method:'post',
          data:formdata,
          headers: {
            'content-type' : 'application/x-www-form-urlencoded'
          }
        }).then(res=>
          console.log(res)
        )
        this.isCreating = false;
        this.findTodoList();
    },
    
    back(){
      this.$router.go(-1);
    },
    createList(){
      this.showCreate = true;
      this.isCreating = true;
      console.log(this.isCreating)
      console.log(111);
  },
  async findTodoList(){
    let todoListRes = await getTodoList();
    this.todoListRes = todoListRes.data;
    console.log(todoListRes)
    if(todoListRes.data.length==0)this.hasTodoList = false;
    if(todoListRes.data.length!=0)this.hasTodoList = true;
  },
  async del(id){
    console.log(id)
    await delTodoList(id);
    this.findTodoList();
    console.log(this.hasTodoList)
  }
}
}
</script>